import React from 'react';
import classes from './Settings.module.css'
import cityDay from '../Common/cityDay.png'
import cityNight from '../Common/cityNight.png'
const Settings = (props) => { 
  
 let lightApp = document.createElement('link');
  lightApp.setAttribute('href', '/static/css/main.85a65f6a.chunk.css');
  lightApp.setAttribute('rel', 'stylesheet');
  lightApp.innerHTML = ``
  
  let darkApp = document.createElement('link');
  darkApp.setAttribute('href', '/static/css/main.85a65f6b.chunk.css');
  darkApp.setAttribute('rel', 'stylesheet');
  darkApp.innerHTML = ``
  

  
 const toSetDarkThemeMode = () => {props.changeThemeAC()};
  
    if (props.theme === false){
 document.head.firstElementChild.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.replaceWith(lightApp)
    } else {
 document.head.firstElementChild.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.nextElementSibling.replaceWith(darkApp)
    }
  
 
  return (
      <div className={classes.editAndSaveButton}>
       {props.theme
          ? <button onClick = {toSetDarkThemeMode}>Night theme: on</button>
          : <button onClick = {toSetDarkThemeMode}>Night theme: off</button>}
       {props.theme
          ? <img src = {cityNight} alt = "cityNight"></img>
          : <img src = {cityDay} alt = "cityDay"></img>}
      </div>
  )
}
export default Settings


